<template>
  <div>
    <van-nav-bar
      title=""
      left-text=""
      left-arrow
      @click-left="onClickLeft"
    />
    <div class="content">
      <van-cell-group>
        <van-field
          :disabled="true"
          required
          label=""
          v-model="userInfoCache.mobile"
          @blur="onBlur('nickname')"
        />
        <van-field
          label=""
          required
          v-model="userInfoCache.nickname"
          @blur="onBlur('nickname')"
        />
        <van-cell title="" is-link>
          <template>
            <van-uploader class="touch-area" :after-read="onRead">
              <template v-if="!userInfoCache.avatar">
                +
                <!-- <img class="icon-add" src="./img/add.png"> -->
              </template>
              <img v-else :src="userInfoCache.avatar" />
            </van-uploader>
          </template>
        </van-cell>
        <!-- <van-cell title="" value="" is-link/>
        <van-cell title="" :value="userInfo.gender==1?'':''"/>
        <van-cell title="" :value="userInfo.email"/>
        <van-cell title="QQ" value=""/>
        <van-cell title="" value=""/> -->
      </van-cell-group>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { NavBar, Field, Toast, Uploader } from 'vant'
import ynowApi from '../../api/ynow'
import * as qiniu from 'qiniu-js'
Vue.use(NavBar)
  .use(Field)
  .use(Uploader)

export default {
  data () {
    return {
      userInfo: {},
      userInfoCache: {},
      pickList: [],
      token: ''
    }
  },
  methods: {
    onClickLeft () {
      this.$router.go(-1)
    },
    onBlur (key) {
      if (
        this.userInfoCache[key] &&
        this.userInfo[key] !== this.userInfoCache[key]
      ) {
        ynowApi
          .updateUserInfo({
            [key]: this.userInfoCache[key]
          })
          .then(res => {
            if (+res.errCode === 0) {
              if (key === 'nickname') {
                Toast('')
              }
            }
          })
      }
    },
    onRead (file) {
      this.pickList[0] = file
      this.uploadImg(this.pickList.length - 1, file.file)
    },
    doGet7nToken () {
      ynowApi.get7nToken().then(res => {
        this.token = res.data.token
      })
    },
    uploadImg (index, file, callback) {
      const vm = this
      const observable = qiniu.upload(file, file.name, this.token, {}, {})
      observable.subscribe({
        next (res) {
          vm.pickList[index]['percent'] = res.total.percent
          vm.pickList = [].concat(vm.pickList)
        },
        error (error) {
          Toast(error)
        },
        complete (res) {
          console.log(res)
          vm.pickList[index]['url'] = `//c1.airtlab.com/${res.key}`
          vm.pickList = [].concat(vm.pickList)
          vm.userInfoCache.avatar = vm.pickList[index]['url']
          vm.onBlur('avatar')
        }
      })
    },
    doCreateProduct () {
      let imgs = this.pickList.map(element => element.url)
      imgs = JSON.stringify(imgs)
      const params = Object.assign(
        {
          imgs
        },
        this.form
      )
      ynowApi.createXzProduct(params).then(res => {
        Toast('')
      })
    },
    doGetUserInfo () {
      ynowApi.getUserInfo().then(res => {
        this.userInfo = res.data
        this.userInfoCache = {
          ...res.data
        }
      })
    }
  },
  mounted () {
    this.doGetUserInfo()
    this.doGet7nToken()
  }
}
</script>

<style scoped>
.touch-area {
  width: 100px;
}
</style>



